html, body, #root {
  height: 100%;
  margin: 0px;
}
code {
  padding: 2px 4px;
  color: black;
  background-color: #DDD;
  border: 1px solid #888;
  border-radius: 6px;
}
@-webkit-keyframes highlight {
  from {
    background-color: var(--highlight);
  }
  to {
    background-color: inherit;
  }
}
@keyframes highlight {
  from {
    background-color: var(--highlight);
  }
  to {
    background-color: inherit;
  }
}



